.footer {
  background-color: #f2f2f2;
  padding: 39px 20px 22px 20px;

    @media (min-width: $tablet-width) {
      display: flex;
      justify-content: space-between;
      padding: 58px 30px 55px 30px;
    }

    @media (min-width: $desktop-width) {
      padding: 57px 110px 58px 110px;
    }
}

.footer__logo-text {
  display: block;
  width: 280px;
  margin: 0 auto;
  text-align: center;

  &::after {
    content: '';
    display: block;
    width: 280px;
    border-bottom: 1px solid #d9d9d9;
    position: absolute;
    top: 90px;

    @media (min-width: $tablet-width) {
      display: none;
    }
  }

  &::before {
    content: '';
    display: block;
    width: 280px;
    border-bottom: 1px solid #d9d9d9;
    position: absolute;
    top: 148px;

      @media (min-width: $tablet-width) {
        display: none;
      }
  }

  @media (min-width: $tablet-width) {
    margin: 0;
    width: 140px;
  }
}

.footer__social {
margin-top: 52px;

  @media (min-width: $tablet-width) {
    margin-top: 0;
  }
}
